* {
    box-sizing: border-box;
}
body {
    margin:0;
    font-family:sans-serif;
    background-color: #282D35;
}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height:90px;
    padding:30px 25px;
}


.nav--logo_text {
    margin:0;
    color:#61DAFB;
    font-size: 22px;
}

.nav--title {
    margin-left: auto;
    color:#DEEBF8;
    font-weight: 500;
}

.nav--icon {
    height: 30px;
    margin-right: 7px;
}

main {
    padding: 60px 30px;
    color:white;
    background-image: url(./images/logobg.png);
    background-repeat: no-repeat;
    background-position-x:50%;
    background-position-y:250%;
    background-size: 400px;
}

.main--title {
    margin: 0;
    font-size: 40px;
    letter-spacing: -0.05em;

}

.main--facts{
    margin-top: 50px;
    max-width: 400px;
}

.main--facts > li {
    line-height: 20px;
    padding-block: 10px;
}

.main--facts > li::marker {
    font-size: 1.5rem;
    color:#61DAFB;
}
